*, :before, :after {
  box-sizing: border-box;
  border: 0;
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
}

:root {
  --dark-cyan: #3c8067;
  --cream: #f2ebe3;
  --very-dark-blue: #1c232b;
  --dark-grayish-blue: #6c7289;
  --white: #fff;
  --font-size: 14px;
  --mobile-card-width: 340px;
  --desktop-card-width: 580px;
}

html, body {
  height: 100%;
}

img {
  width: 100%;
}

body {
  background-color: var(--cream);
  justify-content: center;
  align-items: center;
  display: flex;
}

.card {
  width: var(--desktop-card-width);
  border-radius: 10px;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 1fr;
  height: auto;
  display: grid;
  overflow: hidden;
}

.card > picture {
  display: block;
}

.card > picture img {
  object-fit: cover;
  height: 100%;
  display: block;
}

.card > .card-info {
  background-color: var(--white);
  padding: 32px;
}

.card-info > :first-child {
  color: var(--dark-grayish-blue);
  letter-spacing: 5px;
  text-transform: uppercase;
  margin-bottom: 20px;
  font-size: 12px;
  font-weight: 500;
  display: block;
}

.card-info > h2 {
  color: var(--very-dark-blue);
  margin-bottom: 25px;
  font-family: Fraunces, sans-serif;
  font-size: 32px;
  font-weight: 700;
  line-height: 100%;
}

.card-info > p {
  color: var(--dark-grayish-blue);
  margin-block-end: 25px;
  font-size: 14px;
  font-weight: 500;
  line-height: 165%;
}

.prices {
  align-items: center;
  gap: 20px;
  margin-block-end: 25px;
  display: flex;
}

.prices > :first-child {
  color: var(--dark-cyan);
  font-family: Fraunces, sans-serif;
  font-size: 32px;
  font-weight: 700;
}

.prices > :nth-child(2) {
  color: var(--dark-grayish-blue);
  font-size: 13px;
  font-weight: 500;
  text-decoration: line-through;
}

.card-info > button {
  background-color: var(--dark-cyan);
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  width: 100%;
  padding: 15px 0;
  display: flex;
}

.card-info > button:hover {
  cursor: pointer;
  filter: brightness(85%);
}

.card-info > button > :first-child {
  width: 15px;
}

.card-info > button > :nth-child(2) {
  color: var(--white);
  margin: 0;
  font-size: 14px;
  font-weight: 600;
}

@media (width <= 1000px) {
  .card {
    width: var(--mobile-card-width);
    grid-template-rows: auto 1fr;
    grid-template-columns: 1fr;
  }

  .card > .card-info {
    padding: 23px;
  }

  .card-info > :first-child {
    margin-block-end: 14px;
  }

  .card-info > h2 {
    margin-block-end: 16px;
  }

  .card-info > p {
    margin-block-end: 20px;
  }

  .prices {
    gap: 18px;
  }

  .card-info .prices {
    margin-block-end: 16px;
  }
}

